@import '~plyr/dist/plyr.css';
:root {
  --bg-color: #fff;
  --text-color: #001559;
  --primary-color: #001559;
  --gray: #f1f1f1;
  --accent: #0175ff;
}

.acfe-flexible-placeholder.-preview {
  background: var(--bg-color);
  color: var(--text-color);
}
.acfe-flexible-placeholder.-preview section {
  position: relative;
}
.acfe-flexible-placeholder.-preview,
.acfe-flexible-placeholder.-preview * {
  box-sizing: border-box;
}
.acfe-flexible-placeholder.-preview h1,
.acfe-flexible-placeholder.-preview h2,
.acfe-flexible-placeholder.-preview h3,
.acfe-flexible-placeholder.-preview h4,
.acfe-flexible-placeholder.-preview h5,
.acfe-flexible-placeholder.-preview h6 {
  color: inherit;
}

.acfe-fc-placeholder.acfe-fc-preview .acfe-fc-overlay {
  z-index: 20 !important;
}
.acfe-fc-placeholder.acfe-fc-preview > .button {
  z-index: 25;
}
.acfe-fc-placeholder.acfe-fc-preview:hover .acfe-fc-overlay {
  background: rgba(0, 21, 89, 0.5);
}

.btn {
  display: inline-block;
  padding: 14px 32px;
  border: 2px solid #fff;
  font-size: 16px;
  color: #fff;
  border-radius: 40px;
  cursor: pointer;
  text-decoration: none;
  transition: 0.2s;
}
.btn:hover {
  background: #fff;
  color: #001559;
}
.btn__black {
  border-color: #001559;
  color: #001559;
}
.btn__black:hover {
  background: #001559;
  color: #fff;
}

.-preview .m__title {
  font-weight: 700;
}
.m__title__white {
  color: #fff;
}
.-preview .m__title__white {
  color: #fff !important;
}

.m__desc {
  font-size: 16px;
  line-height: 24px;
  font-weight: 400;
}

.container__center {
  margin-inline: auto;
}
@media (min-width: 768px) {
  .container__short {
    max-width: 90%;
  }
}
@media (min-width: 992px) {
  .container__short {
    max-width: 70%;
  }
}

.-preview .m__hero img {
  max-width: 100%;
}
.m__hero__hero {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  min-height: calc(var(--vh, 1vh) * 100);
}
.-preview .m__hero__hero.-withVideo {
  background: url(../img/placeholder.avif) 50%/cover no-repeat;
}
.m__hero__hero .overlay {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: #000;
  opacity: 0.6;
}
.-preview .m__hero__hero {
  min-height: 600px;
}
.m__hero__hero .container {
  padding: 60px 0;
  margin: 0 auto;
  text-align: center;
  color: #fff;
}
@media (min-width: 768px) {
  .m__hero__hero .container {
    max-width: 90%;
  }
}
@media (min-width: 992px) {
  .m__hero__hero .container {
    max-width: 70%;
  }
}
.m__hero__hero__subtitle {
  font-size: 20px;
  line-height: 30px;
  font-weight: 400;
}
.m__hero__hero__subtitle:not(:first-child) {
  margin-top: 16px;
}
.m__hero__hero__ctas {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  gap: 18px;
  margin-top: 24px;
}
.m__hero__hero__ctas .btn {
  color: inherit;
  border-color: currentColor;
}
.m__hero__hero__ctas .btn:hover {
  background: var(--primary-color);
  color: #fff;
  border-color: var(--primary-color);
}
.m__hero__hero .img {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
}
.m__hero__hero .img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.m__hero__hero .video {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
}
.-preview .m__hero__hero .video {
  display: none;
}
.m__hero__hero .video .plyr {
  height: 100%;
}
.m__hero__hero .video .plyr__video-wrapper {
  position: absolute;
  width: 100%;
  height: 100%;
}
.m__hero__hero .video .plyr__video-wrapper iframe,
.m__hero__hero .video .plyr__video-wrapper video {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.m__hero__hero .video.-full .plyr__video-wrapper {
  left: 50%;
  top: 50%;
  width: auto;
  height: auto;
  min-width: 100%;
  min-height: 100%;
  transform: translate(-50%, -50%);
}
.m__hero .wrapper {
  position: relative;
}

.m__scText {
  padding: 100px 0;
  text-align: center;
}
@media (min-width: 992px) {
  .m__scText__title {
    font-size: 40px;
    line-height: 55px;
  }
}
.m__scText__text {
  text-wrap: balance;
}
.m__scText__text:not(:first-child) {
  margin-top: 30px;
}

.m__ctaBanner {
  background: #001559;
  padding: 55px 0 47px 0;
  text-align: center;
}
@media (min-width: 768px) {
  .m__ctaBanner {
    padding: 75px 67px;
  }
}
.m__ctaBanner__gradient {
  background: linear-gradient(97.03deg, #29A7DD 0%, #001559 92.23%);
}
.m__ctaBanner__title {
  font-weight: 400;
}
.m__ctaBanner__btnWrapper:not(:first-child) {
  margin-top: 30px;
}

.m__gradStats {
  padding: 100px 0 70px;
}
@media (min-width: 768px) {
  .m__gradStats {
    padding: 100px 0 50px;
  }
}
.m__gradStats ._w {
  justify-content: center;
}
.m__gradStats__block {
  margin-bottom: 30px;
}
@media (min-width: 768px) {
  .m__gradStats__block {
    margin-bottom: 50px;
  }
}
.m__gradStats__numb {
  font-size: 80px;
  line-height: 100px;
  font-weight: 700;
  background: linear-gradient(97.03deg, #29A7DD 0%, #001559 92.23%);
  background-clip: text;
  -webkit-text-fill-color: transparent;
  overflow: hidden;
  text-overflow: ellipsis;
}
@media (min-width: 768px) {
  .m__gradStats__numb {
    font-size: 100px;
  }
}
.m__gradStats__desc {
  color: #001559;
  text-wrap: balance;
}

.m__ovrContent h3 {
  font-size: 12px;
}
@media (min-width: 768px) {
  .m__ovrContent h3 {
    font-size: 14px;
  }
}
@media (min-width: 992px) {
  .m__ovrContent h3 {
    font-size: 20px;
  }
}
@media (min-width: 1280px) {
  .m__ovrContent h3 {
    font-size: 24px;
  }
}
.m__ovrContent ._w {
  position: relative;
}
.m__ovrContent__wrapImg {
  width: 80%;
  margin: 0 20px 0 auto;
  padding-top: 60px;
  padding-bottom: 40px;
}
.-preview .m__ovrContent__wrapImg {
  padding: 20px;
}
@media (min-width: 480px) {
  .m__ovrContent__wrapImg {
    margin: 0 40px 0 auto;
    padding-bottom: 20px;
  }
}
@media (min-width: 768px) {
  .m__ovrContent__wrapImg {
    margin: 0 50px 0 auto;
    padding-top: 0;
  }
}
@media (min-width: 992px) {
  .m__ovrContent__wrapImg {
    margin: 0 65px 0 auto;
  }
}
@media (min-width: 992px) {
  .m__ovrContent__wrapImg {
    margin: 0 120px 0 auto;
  }
}
.m__ovrContent__wrapImg__bgImg {
  background: 50%/contain no-repeat #fafafa;
  padding-top: 57.65%;
  height: 0;
}
.m__ovrContent__left {
  position: absolute;
  top: 0;
  left: 0;
  max-width: 80%;
  background: #fff;
  padding: 20px 15px 20px 12px;
}
@media (min-width: 480px) {
  .m__ovrContent__left {
    max-width: 60%;
  }
}
@media (min-width: 768px) {
  .m__ovrContent__left {
    max-width: 40%;
    padding: 40px 15px 40px 12px;
    top: 30px;
  }
}
@media (min-width: 992px) {
  .m__ovrContent__left {
    max-width: 400px;
    top: 35px;
  }
}
@media (min-width: 1280px) {
  .m__ovrContent__left {
    max-width: 507px;
    top: 35px;
  }
}
.m__ovrContent__left__text {
  display: -webkit-box;
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-line-clamp: 5;
  -webkit-box-orient: vertical;
}
@media (max-width: 767.98px) {
  .m__ovrContent__left__text {
    font-size: 16px;
  }
}
@media (min-width: 768px) {
  .m__ovrContent__left__text {
    -webkit-line-clamp: 7;
    width: 100%;
  }
}
@media (min-width: 1280px) {
  .m__ovrContent__left__text {
    -webkit-line-clamp: 10;
  }
}
.-preview .m__ovrContent__left__text {
  font-size: 14px;
}
.m__ovrContent__right {
  padding: 10px 15px;
  position: absolute;
  bottom: 0;
  right: 12px;
  max-width: 75%;
  background: #001559;
  color: #fff;
}
@media (min-width: 480px) {
  .m__ovrContent__right {
    max-width: 60%;
  }
}
@media (min-width: 768px) {
  .m__ovrContent__right {
    padding: 15px;
    max-width: 55%;
  }
}
@media (min-width: 992px) {
  .m__ovrContent__right {
    padding: 20px 30px;
    max-width: 55%;
  }
}
@media (min-width: 992px) {
  .m__ovrContent__right {
    padding: 20px 30px;
    width: 85%;
  }
}
.m__ovrContent__right__text {
  display: -webkit-box;
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  text-wrap: balance;
}
@media (min-width: 480px) {
  .m__ovrContent__right__text {
    width: 80%;
  }
}
@media (max-width: 767.98px) {
  .m__ovrContent__right__text {
    font-size: 16px;
  }
}
.-preview .m__ovrContent__right__text {
  font-size: 14px;
}

.m__statistic {
  background: linear-gradient(90deg, #27A4D9 0%, #032164 46.35%, #DE252B 98.44%);
  margin: 50px 0;
  padding-top: 60px;
  color: #fff;
}
@media (min-width: 768px) {
  .m__statistic {
    margin: 100px 0;
    padding-top: 60px;
  }
}
@media (max-width: 479.98px) {
  .m__statistic__top__title {
    font-size: 30px;
  }
}
@media (min-width: 1280px) {
  .m__statistic__top__title {
    line-height: 65px;
  }
}
.m__statistic__bot {
  position: relative;
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .m__statistic__bot {
    flex-direction: row;
  }
}
.-preview .m__statistic__bot {
  top: 15px;
}
.m__statistic__bot__numb {
  position: relative;
  top: 25px;
  font-size: 80px;
  line-height: 1;
  font-weight: 700;
  white-space: nowrap;
  align-self: flex-end;
}
@media (min-width: 480px) {
  .m__statistic__bot__numb {
    font-size: 100px;
  }
}
@media (min-width: 768px) {
  .m__statistic__bot__numb {
    font-size: 130px;
    top: 30px;
  }
}
@media (min-width: 992px) {
  .m__statistic__bot__numb {
    font-size: 200px;
    top: 45px;
  }
}
@media (min-width: 1280px) {
  .m__statistic__bot__numb {
    font-size: 300px;
  }
}
.-preview .m__statistic__bot__numb {
  font-size: 100px;
  top: 0;
}
.m__statistic__bot__desc {
  order: -1;
}
.m__statistic__bot__desc:not(:first-child) {
  margin-top: 30px;
}
@media (min-width: 768px) {
  .m__statistic__bot__desc {
    order: 1;
    padding: 45px 0 20px 10px;
  }
  .m__statistic__bot__desc:not(:first-child) {
    margin-top: 0;
  }
}
@media (min-width: 992px) {
  .m__statistic__bot__desc {
    padding: 60px 0 20px 10px;
  }
}

.m__logoReel {
  padding: 80px 0;
}
@media (min-width: 768px) {
  .m__logoReel {
    padding: 100px 0;
  }
}
.m__logoReel__title, .m__logoReel__desc {
  text-align: center;
}
.m__logoReel__desc {
  text-wrap: balance;
}
.m__logoReel__desc:not(:first-child) {
  margin-top: 25px;
}
@media (min-width: 768px) {
  .m__logoReel__desc:not(:first-child) {
    margin-top: 35px;
  }
}
@media (min-width: 992px) {
  .m__logoReel__desc {
    font-size: 20px;
  }
}
@media (min-width: 1280px) {
  .m__logoReel__desc {
    font-size: 24px;
  }
}
.m__logoReel__logos {
  display: flex;
}
.m__logoReel__logos:not(:first-child) {
  margin-top: 40px;
}
@media (min-width: 768px) {
  .m__logoReel__logos:not(:first-child) {
    margin-top: 50px;
  }
}
.m__logoReel__logos__block:not(:first-child) {
  padding-left: 60px;
}
.m__logoReel__logos__block__img {
  height: 45px;
  width: auto;
}
.-preview .m__logoReel__logos__block__img {
  height: 30px;
}

.m__steps__wrapper {
  position: relative;
}
@media (min-width: 768px) {
  .m__steps__wrap {
    display: flex;
  }
}
.m__steps__wrap:not(:first-child) {
  margin-top: 20px;
}
.m__steps__banner {
  padding: 25px 30px;
  width: 100%;
}
.m__steps__banner:before {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: white;
  clip-path: polygon(92% 0%, 100% 50%, 92% 100%, 0% 100%, 0% 50%, 0% 0%);
  z-index: -1;
}
.m__steps__blockWrap {
  width: 100%;
  position: relative;
}
.m__steps__blockWrap__bgImg {
  background: 50%/cover no-repeat #fafafa;
  padding-top: 89.27%;
  height: 0;
}
.m__steps__full .m__steps__banner {
  position: absolute;
  z-index: 10;
  left: 0;
  bottom: 0;
  transform: translateY(50%);
}
@media (min-width: 768px) {
  .m__steps__full .m__steps__banner {
    display: none;
  }
}
.m__steps__onlyText {
  display: none;
}
@media (min-width: 768px) {
  .m__steps__onlyText {
    display: flex;
    position: absolute;
    top: 50%;
    left: 0;
    transform: translateY(-50%);
  }
}